n. Wir grenzen uns deshalb davon ab, ein Sportverein zu sein. Bei uns steht die Frderung der Skateboardkultur in seiner gesamten Bandbreite im Vordergrund!"),_c('br'),_c('br'),_vm._v("\n\n       Ziele von Heidelberg Skateboarding e. V sind.:"),_c('br'),_c('br'),_c('ul',[_c('li',[_vm._v(" Die Frderung und Untersttzung der Skateboardkultur in Heidelberg und Umgebung  durch die Veranstaltung von verschiedenen Events, Contests, Skatekursen und das damit verbundene Zusammenkommen der regionalen und berregionalen Skateboardgemeinschaft. ")]),_c('li',[_vm._v(" Kinder, Jugendliche und Erwachsene fr Skateboarding zu begeistern. ")]),_c('li',[_vm._v(" Kindern, Jugendlichen und Erwachsenen die Mglichkeit geben zusammen zu spielen (zu skaten) und sich dabei frei zu entfalten. ")]),_c('li',[_vm._v(" Gegen Vorurteile und Repression von Minderheiten und SkateboarderInnen zu kmpfen. ")]),_c('li',[_vm._v(" Eine positive Gemeinschaft zu bilden in der sich jeder wohl fhlt, unabhngig von Geschlecht, Alter, Hautfarbe, Muttersprache oder Meinung. ")]),_c('li',[_vm._v(" Eine Rolle als kommunizierendes und beratendes Bindeglied zwischen der Skateboardgemeinschaft und der Stadt einzunehmen um die Einflussnahme auf den Bau und Erhalt von Skateparks zu vergrern, sowie das Auftreten als Sprachrohr fr alle Skateboard relevanten Belangen. ")])])]),_c('h1',{attrs:{"id":"antrag"}},[_vm._v("\n          Mitgliederantrag \n       ")]),_c('p',[_vm._v("\n         Wenn du kein Geld hast sag Bescheid dann kannst du auch irgendwie umsonst Mitglied werden.  \n         Ansonsten betrgt der Beitragssatz fr Mitglieder:\n         \n         "),_c('ul',[_c('li',[_vm._v("\n         Unter 18: 3  im Monat\n           ")]),_c('li',[_vm._v("\n         ber 18: 5 im Monat\n           ")]),_c('li',[_vm._v("\n         Frdermitgliedsbeitrag: 2  im Monat\n           ")])]),_vm._v("\n\n         Der "),_c('a',{attrs:{"href":"Beitrittserklaerung.pdf","download":""}},[_vm._v("Mitgliederantrag")]),_vm._v(" muss ausgedruckt und